

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #e2a5a5, #e2a5a5); /* Gradient background */
  height: 100vh; /* Full height */
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-fluid {
  max-width: 800px; /* Set maximum width for the container */
  margin: 0 auto; /* Center the container horizontally */
}

h2 {
  color: #000000; /* Dark gray color for headings */
  margin-bottom: 20px; /* Add some space below the heading */
}

/* Style for Login component */
.login-container {
  background-color: #f9f9f9; /* Light gray background color */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Box shadow for a subtle effect */
  max-width: 400px;
  margin: 0 auto; /* Center the container horizontally */
}

.login-container div {
  margin-bottom: 15px;
}

.login-container button {
  padding: 8px 16px;
  background-color: #3498db; /* Blue background color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-container button:hover {
  background-color: #0d7ac3; /* Darker blue color on hover */
}
